.chevron::before {
  border-style: solid;
  border-width: 0.25em 0.25em 0 0;
  content: "";
  display: inline-block;
  height: 0.45em;
  transform: rotate(-45deg);
  vertical-align: top;
  width: 0.45em;
  cursor: pointer;
  color: white;
  margin: 0.5rem;
}

.chevron.left:before {
  left: 0.25em;
  transform: rotate(-135deg);
}

.chevron.right:before {
  left: 0;
  transform: rotate(45deg);
}

.chevron.up:before {
  left: 0.25em;
  transform: rotate(-45deg);
}

.chevron.down:before {
  left: 0;
  transform: rotate(135deg);
}

.fade-in {
  animation: fadeIn ease 0.45s;
  -webkit-animation: fadeIn ease 0.45s;
  -moz-animation: fadeIn ease 0.45s;
  -o-animation: fadeIn ease 0.45s;
  -ms-animation: fadeIn ease 0.45s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
